.container {
  position: relative;
  border-radius: 10px;
  background-color: #13171c;
  border: 1px solid #121b0f;
  box-shadow: inset 0 0 4px 0 #00000070;
}

.pagination {
  min-width: 52px;
  margin-left: 10px;
  overflow: hidden;

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;

    & > button ~ button {
      margin-top: 10px;
    }
  }
}

.cards {
  width: 100%;

  &__content {
    position: absolute;
    height: 100%;
    width: 100%;
    border-radius: 10px;
    overflow: hidden;

    & > div {
      padding: 10px;
      height: 100%;
      overflow-y: auto;
      scrollbar-width: 6px;
      scrollbar-color: #9fa268 transparent;

      &::-webkit-scrollbar {
        width: 6px;
      }

      &::-webkit-scrollbar-track {
        background: #00000070;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #9fa268;
        border-radius: 20px;
      }
    }
  }
}
